import React, { Component } from "react";

import { Button, Input } from "antd";
import "./index.less";
export default class index extends Component {
  state = {
    bigArray: "",
    outputArray: []
  };
  handleInputChange = e => {
    console.log(e.target.value.length);

    this.setState({
      bigArray: e.target.value
    });
  };
  handleArray = e => {
    let value = this.refs.bigArray.props.value;
    value = value.split(",");

    let outputArray = this.getAllSubsets(value);
    this.setState({
      outputArray
    });
    console.log(JSON.stringify(outputArray));
  };
  getAllSubsets = theArray =>
    theArray.reduce(
      (subsets, value) => subsets.concat(subsets.map(set => [value, ...set])),
      [[]]
    );
  componentDidMount() {}
  render() {
    return (
      <div>
        <h2>1-</h2>
        <div className="content">
          <h3></h3>
          <p></p>
          <p></p>
          <p> [1,2]</p>
          <p> [], [1], [2], [1,2]</p>
          <p></p>

          <h3></h3>
          <p></p>
          <h3></h3>
          <p>1,2</p>
          <Input
            style={{ width: 300, marginRight: 20 }}
            ref="bigArray"
            placeholder=""
            onChange={this.handleInputChange}
            value={this.state.bigArray}
          />
          <Button type="primary" onClick={this.handleArray}>
            
          </Button>
          <p />
          <p></p>
          {JSON.stringify(this.state.outputArray)}
        </div>
      </div>
    );
  }
}
